/* src/components/Grid.css */
.grid {
    display: flex;
    flex-direction: column;
    border: 1px solid #ccc;
  }
  
  .grid-row {
    display: flex;
  }
  
  .grid-cell {
    border: 1px solid #ddd;
    padding: 8px;
    min-width: 60px;
    min-height: 30px;
    text-align: center;
    cursor: pointer;
    user-select: none;
  }
  
  .grid-cell input {
    width: 100%;
    height: 100%;
    border: none;
    padding: 0;
    outline: none;
  }
  
  .grid-cell.active {
    background-color: #eef;
  }
  